import action from '../../../components/action/action.vue'
export default {
	components: {
		action
	},
	data() {
		return {
			font:'全部',
			Font:'全部',
			is_auth:'',
			listData: [],
			list:['全部'],
			List:['全部','已认证','未认证'],
			page: 1,
			size: 10,
			loading: false,
			finished: false,
			typeid:'',
			value:'',
			more: true,
			all:''
		};
	},
	methods:{
		link(id){
			uni.navigateTo({
				url: `/pages/mine/ally/message?id=${id}`
			})
		},
		//状态
		screen(){
			this.$refs.action.actionConfig={
				list:this.list,
				type:0,
				isBorderColor:false,
				specClass: 'show'
			}
		},
		itemclick(index,type){
			this.font = this.list[index]
			this.typeid = index	
		},
		
		// 时间
		sort(){
			this.$refs.Action.actionConfig={
				list:this.List,
				type:0,
				isBorderColor:false,
				specClass: 'show'
			}
		},
		itemClick(index,type){
			this.Font = this.List[index]
			if(index != 0){
				this.is_auth = index
			}else{
				this.is_auth = ''
			}
		},
		getData(){
			this.$store.dispatch('getMyTeam',{
				page: this.page,
				size: this.size,
				// typeid: 0
			}).then(res=>{
				this.listData = res.data
			}).catch(err=>{})
		},
		sousuo(){
			this.$store.dispatch('teamtotal',{
				typeid:this.typeid,
				is_auth:this.is_auth,
				value:this.value
			}).then(res=>{
				this.all = res.data
			}).catch(err=>{})
		}
	},
	onShow(){
		this.sousuo()
		this.$store.dispatch('getMyTeam',{
			page: this.page,
			size: this.size,
			// typeid: 0
		}).then(res=>{
			this.listData = res.data
		}).catch(err=>{})
		this.$store.dispatch('getLevelList',{}).then(res=>{
			for(var i = 0;i < res.data.length;i++){
				this.list.push(res.data[i].name)
			}
		}).catch(err=>{})
	},
	onPullDownRefresh() {
		this.more = true
		this.$store.dispatch('getMyTeam',{
			page: this.page,
			size: this.size,
			// typeid: 0
		}).then(res=>{
			this.listData = res.data
			uni.stopPullDownRefresh()
		}).catch(err=>{})
	},
	onReachBottom() {
		if (this.more) {
			this.page++
		}
		this.more = false
		this.$store.dispatch('getMyTeam',{
			page: this.page,
			size: this.size,
			// typeid: 0
		}).then(res=>{
			this.more = true;
			if (this.page > 1) {
				var dataArr = this.listData;
				res.data.forEach(item => {
					dataArr.push(item)
				});
				this.listData = dataArr;
			} else {
				this.listData = res.data;
			}
		}).catch(err=>{})
	}
}
